import logo from "./logo.svg";
import "./App.css";
import ItemCard from "./itemCard";
import { useState } from "react";

function App() {
  let itemList = [
    {
      itemName: "BoAt Speaker",
      price: 49,
      currency: "$",
      description:
        "Power Output (RMS): 5 W, Battery life: 4 hr | Charging time: 1.5 hr, Bluetooth Version: 5, Wireless range: 10 m, Wireless music streaming via Bluetooth",
    },
    {
      itemName: "Intex Speaker",
      price: 29,
      currency: "$",
      description:
        "Power Output (RMS): 55 W, Power Source: AC Adapter, Bluetooth Version: 4.2, Wireless range: 8 m, Wireless music streaming via Bluetooth",
    },
    {
      itemName: "Philips Speaker",
      price: 69,
      currency: "$",
      description:
        "Power Source: 5 V DC,Battery life: 4 hr | Charging time: 2 Hours, Bluetooth Version: 4.1, Wireless range: 10 m",
    },
    {
      itemName: "LG Speaker",
      price: 99,
      currency: "$",
      description:
        "Power Output (RMS): 5 W, Power Source: AC Adapter, Battery life: 10 m | Charging time: 4 hrs",
    },
    {
      itemName: "JBL Speaker",
      price: 149,
      currency: "$",
      description:
        "Power Output (RMS): 16 W, Battery life: 10 hr | Charging time: 3.5 hr, Bluetooth Version: 4.1, Wireless music streaming via Bluetooth",
    },
  ];
  const [currentCart, setCurrentCart] = useState([]);
  const [total, setTotal] = useState(0);

  var cartlist = [];
  let handleSelectedItem = (item) => {
    currentCart.push(item);
    setCurrentCart([...currentCart]);
    let newtotal = total + item.price;
    setTotal(newtotal);
  };

  function handleRemoveItem(index) {
    let newtotal = total - currentCart[index.index].price;
    currentCart.splice(index, 1);
    setCurrentCart([...currentCart]);
    setTotal(newtotal);
  }

  return (
    <div className="container">
      <div className="row">
        <div className="col-3 bg-light">
          <h4 className="bg-">Cart (selected items)</h4>
          <ul className="list-group list-group-flush">
            {currentCart.map((item, index) => {
              return (
                <li className="list-group-item entry">
                  {item.itemName}
                  <br></br>
                  {item.currency}
                  {item.price}
                  <span
                    className="btn btn-success float-right"
                    onClick={() => {
                      handleRemoveItem({ index });
                    }}
                  >
                    Remove
                  </span>
                </li>
              );
            })}
            <li className="list-group-item"><h5>Total = ${total}</h5></li>
          </ul>
        </div>
        <div className="col-9">
          <div className="row">
            {itemList.map((item) => {
              return (
                <ItemCard
                  data={item}
                  handlebtnclick={handleSelectedItem}
                ></ItemCard>
              );
            })}
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
